<template>
  <div>
    <div class="row">
      <h4>Technical Skills</h4>
      <div class="input-group mb-3">
        <span class="input-group-text">
          <fa icon="cogs" />
        </span>
        <input
          type="text"
          class="form-control"
          placeholder="Type Your Technical Skill"
          v-model="skillname"
        />
        <button class="input-group-text" @click.prevent="addfield()" :disabled="!skillname">
          <fa icon="plus" /> &nbsp; Add Skill
        </button>
      </div>

      <div
        class="skills-list input-group mb-3"
        v-for="(skill, index) in skills"
        :key="index"
      >
        <input type="text" class="form-control" disabled :value="skill" />
        <button class="input-group-text" @click="deletefield(index)">
          <fa icon="minus" /> &nbsp; Delete Skill
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "technical",
  data: function () {
    return {
      skills: [],
      skillname: ""
    };
  },
  methods: {
    addfield() {
      this.skills.push(this.skillname);
      this.skillname = "";
    },
    deletefield(index) {
      this.skills.splice(index, 1);
    }
  }
};
</script>
