<template>
  <idv>
    <div class="row">
      <h4>Languages</h4>
      <div class="col-md-6">
        <div class="Langs d-flex">
          <div class="Lang">
            <input type="checkbox" id="Arabic" value="Arabic" v-model="Langs" />
            <label for="Arabic">&nbsp;Arabic</label>
          </div>
          <div class="Lang">
            <input
              type="checkbox"
              id="English"
              value="English"
              v-model="Langs"
            />
            <label for="English">&nbsp;English</label>
          </div>
          <div class="Lang">
            <input type="checkbox" id="French" value="French" v-model="Langs" />
            <label for="French">&nbsp;French</label>
          </div>
        </div>
        <div class="selected_langs">
          <fa icon="language" /> The Selected Languages :
          <span v-for="Lang in Langs" :key="Lang">{{ Lang }}</span>
          <span>{{ othertextfilter() }}</span>
        </div>
      </div>
      <div class="col-md-1"></div>
      <div class="col-md-5">
        <div class="Langs">
          <div class="Lang">
            <input
              type="checkbox"
              id="Other"
              value="Other"
              v-model="langsother"
            />
            <label for="Other">&nbsp; Other</label>
            <input
              v-if="this.langsother == true"
              type="text"
              class="form-control"
              placeholder="Type Your Languages Like .... / ...."
              v-model="othertext"
            />
          </div>
        </div>
      </div>
    </div>
  </idv>
</template>

<script>
export default {
  name: "languages",
  data: function () {
    return {
      Langs: ["Arabic"],
      langsother: false,
      othertext: ""
    };
  },
  methods: {
    othertextfilter() {
      var text = this.othertext;

      text = text.replace(/\//g, " ");
      return text;
    }
  }
};
</script>
