<template>
  <div class="courses-pic">
    <div class="row">
      <h4>Technical Courses</h4>
      <div class="col-md-8">
        <div class="input-group mb-3">
          <span class="input-group-text">
            <fa icon="medal" />
          </span>
          <input
            type="text"
            class="form-control"
            placeholder="Type Your Technical course"
            v-model="coursename"
          />
          <button
            class="input-group-text"
            @click.prevent="addfield()"
            :disabled="!coursename"
          >
            <fa icon="plus" /> &nbsp; Add course
          </button>
        </div>
        <div class="list">
          <div
            class="course-list input-group mb-3"
            v-for="(course, index) in courses"
            :key="index"
          >
            <input type="text" class="form-control" disabled :value="course" />
            <button
              class="input-group-text"
              @click.prevent="deletefield(index)"
            >
              <fa icon="minus" /> &nbsp;
            </button>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <h4>Select Your Image | QR Code</h4>
        <div class="mb-3">
          <label for="formFileSm" class="form-label d-flex">
            <fa icon="image" />
          </label>
          <input
            class="form-control filepic form-control-sm d-none"
            id="formFileSm"
            type="file"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.list {
  min-height: 130px;
  border: 1px dashed #e9ecef;
  border-radius: 10px;
  padding: 5px;
}

.courses-pic {
  label {
    width: 100%;
    min-height: 150px;
    background-color: #241570;
    box-shadow: 12px 12px 32px #0000000d;
    color: #fff;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    svg {
      font-size: 4em;
    }
  }
  .ownpic {
    background-color: #241570;
    color: #fff;
    padding: 5px;
    border-radius: 5px;
  }
}
</style>

<script>
export default {
  name: "coursespic",
  data: function () {
    return {
      courses: [],
      coursename: "",
      picsrc: "",
    };
  },
  methods: {
    addfield() {
      this.courses.push(this.coursename);
      this.coursename = "";
    },
    deletefield(index) {
      this.courses.splice(index, 1);
    },
  },
};
</script>
