<template>
  <div class="exp">
    <div class="row">
      <h4>Experience</h4>
      <div class="col-md-8">
        <div class="input-group mb-3">
          <span class="input-group-text">
            <fa icon="certificate" />
          </span>
          <input
            type="text"
            class="form-control"
            placeholder="Type The Job Title"
            v-model="prevjobtitle"
          />
        </div>

        <div class="input-group mb-3">
          <span class="input-group-text">
            <fa icon="calendar-day" />
          </span>
          <input
            type="month"
            class="form-control dayfrom"
            placeholder="Type The Job Title"
            v-model="prevdatefrom"
          />

          <span class="input-group-text">
            <fa icon="calendar-alt" />
          </span>
          <input
            type="month"
            class="form-control"
            placeholder="Type The Job Title"
            v-model="prevdateto"
          />
        </div>

        <div class="input-group mb-3">
          <span class="input-group-text">
            <fa icon="certificate" />
          </span>
          <input
            type="text"
            class="form-control"
            placeholder="Where It Was"
            v-model="prevjobwhere"
          />
        </div>

        <div class="input-group mb-3">
          <span class="input-group-text">
            <fa icon="certificate" />
          </span>
          <input
            type="text"
            class="form-control responsa"
            placeholder="What Were The Resposabities"
            v-model="prevresposname"
          />
          <button
            class="input-group-text"
            @click.prevent="addrespon()"
            :respons="!skillname"
          >
            <fa icon="plus" /> &nbsp; Add
          </button>
        </div>

        <!-- Add Panel Exp -->

        <div class="input-group mb-3">
          <input
            type="submit"
            value="Insert EXP"
            class="form-control btn subbutton"
            @click.prevent="addEXP()"
          />
        </div>
      </div>

      <!-- Preview Area -->

      <div class="col-md-4">
        <div class="preview">
          <div class="card">
            <div class="card-header">
              <h5>{{ prevjobtitle }}</h5>
              <h6>{{ prevjobwhere }}</h6>
            </div>
            <div class="card-body">
              <blockquote class="blockquote mb-0">
                <ul type="none">
                  <li v-for="(prop, index) in prevrespontitle" :key="index">
                    <fa icon="minus" @click="removerespon(index)" />
                    {{ prop }}
                  </li>
                  <li>{{ prevresposname }}</li>
                </ul>
                <footer class="blockquote-footer">
                  Duration :
                  <cite title="Source Title"
                    ><span>({{ prevdatefrom }} - {{ prevdateto }})</span></cite
                  >
                </footer>
              </blockquote>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-md-4 old-exp" v-for="(exp, index) in exps" :key="index">
        <div class="card">
          <div class="card-header">
            <fa icon="minus" @click="removeEXP(index)"/>
            <h5>
              {{ exp.jobtitle }}
            </h5>
            <h6>{{ exp.jobwhere }}</h6>
          </div>
          <div class="card-body">
            <blockquote class="blockquote mb-0">
              <ul>
                <li v-for="(num, index) in exp.respons" :key="index">
                  {{ num }}
                </li>
              </ul>
              <footer class="blockquote-footer">
                Duration :
                <cite title="Source Title"
                  ><span>({{ exp.datefrom }} - {{ exp.dateto }})</span></cite
                >
              </footer>
            </blockquote>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.dayfrom {
  margin-right: 10px;
}

.subbutton {
  background-color: #241570;
  color: #fff;
}

.preview {
  ul {
    padding-left: 0;
    svg {
      background-color: #00000008;
      padding: 3px 5px;
      cursor: pointer;
    }
  }
  h5,
  h6 {
    margin-bottom: 0;
  }
}

.old-exp {
  position: relative;
  margin-bottom: 15px;
  svg {
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: #fff;
    color: #00f;
    border-radius: 3px;
    padding: 5px;
    font-size: 22px;
    cursor: pointer;
  }
  .card-header {
    background-color: #241570;
    color: #fff;
    h5,
    h6 {
      margin-bottom: 0;
      span {
        font-weight: normal;
      }
    }
  }
}
</style>

<script>
export default {
  name: "exp",
  data: function () {
    return {
      prevjobtitle: "Title Of Job",
      prevdatefrom: "",
      prevdateto: "",
      prevjobwhere: "Governement, City",
      prevresposname: "Tasks And Responsabilities",
      prevrespontitle: [],
      exps: [],
    };
  },
  methods: {
    addrespon() {
      this.prevrespontitle.push(this.prevresposname);
      this.prevresposname = "";
    },
    removerespon(index) {
      this.prevrespontitle.splice(index, 1);
    },
    addEXP() {
      this.exps.push({
        jobnum: "",
        jobtitle: this.prevjobtitle,
        datefrom: this.prevdatefrom,
        dateto: this.prevdateto,
        jobwhere: this.prevjobwhere,
        respontitle: "",
        respons: this.prevrespontitle,
      });
    },
    removeEXP(index) {
      this.exps.splice(index, 1);
    }
  },
};
</script>
